.nav {
  /*grid-area: n;*/
  background-color: transparent;
  color: aliceblue;
}

.navList {
  width: 800px;
  height: 50px;
  margin: 0 auto;
  text-align: center;
  vertical-align:middle;
  display: flex;
  justify-content: center;
}

.nav a {
  text-decoration: none;
  color: aliceblue;

}

.nav a:hover {
  color: crimson;
}

.item {
  width: 100px;
  padding-top: 20px;
}

.item a.active {
  color: crimson;
}

.friendsList {
  display: flex;
  justify-content: space-around;
  list-style:none;
}

.friendsItem {
  position: relative;
  margin-top: 5px;
}

.friendsItem:after {
  content: '';
  width: 20px;
  height: 20px;
  background-color: yellow;
  border-radius: 50%;
  position: absolute;
  left: 17px;
  bottom: 21px;
}

.friendsTitle {
  text-align: center;
  font-size: 24px;
}



